.feauters {
	position: relative;
	background-color: $c-text-blue;
	padding-top: 200px;
	padding-bottom: 160px;
	overflow: hidden;

	&__photo {
		top: -600px;
		left: -10px;
		position: absolute;
		width: 3200px;
		height: 480px;
		transform: rotate(-65.1deg);
		overflow: hidden;

		& img {
			position: absolute;
			transform: rotate(65.1deg);
		}
	}

	&__wrapper {
		max-width: 365px;
	}

	&__title {
		font-weight: 900;
		font-size: 32px;
		line-height: 140%;
	}

	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;

		&:not(:last-child) {
			padding-bottom: 64px;
		}
	}

	&__icon {
		height: 64px;
		width: 240px;
		margin-right: 32px;
		background-position: center;
		background-repeat: no-repeat;
	}

	&__text {
		font-size: 16px;
		line-height: 150%;
		color: $c-text-white;
	}

	.icon-1 {
		background-image: url("../../images/feauters-icon1.png");
	}

	.icon-2 {
		background-image: url("../../images/feauters-icon2.png");
	}

	.icon-3 {
		background-image: url("../../images/feauters-icon3.png");
	}
}
