.info {
	padding: 50px 0;

	&__wrapper {
		display: flex;
		justify-content: space-between;
	}

	&-massage {
		display: flex;
		flex-direction: column;
		width: 55%;

		&__section {
			padding-bottom: 100px;
		}

		& input {
			width: 100%;
			margin: 25px 0;
			outline: none;
			border-bottom: 1px solid #e1e1e1;
			padding-bottom: 18px;
			box-sizing: border-box;
			font-weight: bold;
			font-size: 14.4px;
			line-height: 150%;

			&:hover {
				border-bottom: 1px solid $c-text-blue;
			}

			&:focus {
				border-bottom: 1px solid $c-text-blue;
			}
		}

		&__button {
			margin-top: 75px;
			box-sizing: border-box;
			background-color: $c-button;
			color: $c-text-white;
		}
	}

	&-contact {
		display: flex;
		flex-direction: column;
		width: 35%;

		&__title {
			font-weight: 900;
			font-size: 32px;
			line-height: 140%;
			padding-bottom: 60px;
		}

		&__link {
			font-weight: 800;
			font-size: 12.6px;
			line-height: 150%;
		}

		&__info {
			letter-spacing: 2px;
			text-transform: uppercase;
			font-weight: 900;
			font-size: 24px;
			line-height: 150%;
			text-decoration: none;
			color: $c-text-blue;
			padding-bottom: 45px;
		}

		&__social-icons {
			display: flex;
			justify-content: space-between;
			padding-top: 5px;
			max-width: 192px;
		}

		&__icon {
			background-repeat: no-repeat;
			height: 32px;
			width: 32px;
			cursor: pointer;
			transition: transform 0.3s ease-in-out;

			&:hover {
				transform: scale(1.05);
			}

			&:active {
				transform: scale(0.95);
			}
		}
	}

	.twitter__img {
		background-image: url("../../images/twitter.svg");
	}

	.instagram__img {
		background-image: url("../../images/instagram.svg");
	}

	.facebook__img {
		background-image: url("../../images/facebook.svg");
	}
}